import { Link } from 'react-router-dom'
const Navbar = () => {
    return ( 
        <nav className="navbar">
            <div className="link">
                <span className="head">Blog</span>
                <span className="navlink">
                    <Link to="/">Home</Link>
                    <Link to="/NewBlog">New Blog</Link>
                    <Link to="/About" className="blog">About Us</Link>
                </span>    
            </div>
        </nav>
     );
}
 
export default Navbar;