import BlogList from './BlogList';
import useFetch from './useFetch';
const Home = () => {
    const { data: blogs, loading, error } = useFetch('http://localhost:8000/blogs'); //http://jsonplaceholder.typicode.com/posts
    return (
        <div className="home">
            { error && <div className="error"> {error} </div>}
            { loading && <div className="loading">Loading...</div> }
            { blogs && <BlogList title = "All Blogs" blogs= {blogs} /> }
            { blogs && <BlogList title = "Arun Blogs" blogs= {blogs.filter((blog) => 
                blog.author === "Arun")} /> } 
        </div>
    );
}

export default Home;